import { useClient } from "react";
import { api } from "~/utils/api"; // Ensure this points to your trpc API
import NavigationBar from '../NavigationBar/page';


const generateFeedback = api.feedback.generateFeedback.useMutation();


const validFeedbackTypes = ["improvement", "strengths", "parentConference"] as const;

type FeedbackType = typeof validFeedbackTypes[number];

const isValidFeedbackType = (value: string): value is FeedbackType => {
  return validFeedbackTypes.includes(value as any);
};

const SearchStudent = () => {
  const [studentId, setStudentId] = useClient("");
  const [feedbackType, setFeedbackType] = useClient<FeedbackType | "">("");

  const handleGenerateFeedback = () => {
    if (!studentId || !feedbackType) return;
    generateFeedback.mutate({ studentId, feedbackType });

  };

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold mb-4">Search Student</h1>
      <input
        type="text"
        className="border p-2 w-full mb-2"
        placeholder="Enter Student ID"
        value={studentId}
        onChange={(e) => setStudentId(e.target.value)}
      />
      <select
        className="border p-2 w-full mb-2"
        value={feedbackType}
        onChange={(e) => {
          const value = e.target.value;
          if (isValidFeedbackType(value)) {
            setFeedbackType(value);
          }
        }}
      >
        <option value="">Select Feedback Type</option>
        <option value="improvement">Improvement</option>
        <option value="strengths">Strengths</option>
        <option value="parentConference">Parent Conference</option>
      </select>
      <button
        className="bg-blue-500 text-white px-3 py-2 rounded"
        onClick={handleGenerateFeedback}
        disabled={!studentId || !feedbackType}
      >
        Generate Feedback
      </button>
    </div>
  );
};

export default SearchStudent;